@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@600&family=Comic+Neue&display=swap);
html{
  scroll-behavior: smooth;
}
:root{


  --first-color: #8a8fb2;
  --second-color:#a1ddf8;
  --third-color:#8083ff;

  --White-color:#eaeeff;
  --Black-color:#3c4a3e;

  --first-font-family: 'Comfortaa', cursive;
  --second-font-family:'Comic Neue', cursive;

  --first-font-size: 2rem;
  --second-font-size:1rem;




}
html{
  scroll-behavior: smooth;
  overflow-x: hidden;

}

*{
  margin:0;
}

.ravishankar-hi{
  /* background-image: url('./component/ravi.svg') ; */
  background-repeat: no-repeat;
  display: flex;
 
  text-align: center;
  justify-content: center;

  font-family: 'Comfortaa', cursive;

  font-family: var(--first-font-family);

}

.ravishankar-img-container{
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9;
  /* width: 80%;
  height: auto; */
}
.ravishankar-img{
  display: flex;
  justify-content: center;
} 
.ravishankar-img img{
  
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #8083ff;
  background-color: var(--third-color);
  border-radius: 50%;
  width: 80%;
}
.home-start{
  margin-top: 2%;
  z-index: 9;
}
.home-start-row{
  height: 33vh;

}
.row-intro{
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  text-align: center;
  margin: 50px;
  z-index: 90;

  
}
.intro{
  font-weight: bold;
  font-size: 2rem;
  font-size: var(--first-font-size);
  text-transform: uppercase;
  font-family: 'Comic Neue', cursive;
  font-family: var(--second-font-family);
}

.carousel-caption h5,p{
  color:#3c4a3e;
  color:var(--Black-color);



}

.carousel-item {
  display:flex;
  align-items: center;
  justify-content: center;
}


.work{
  display: flex;
  justify-content: space-around;
}
.card{
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.card > img{

  width: 70px;;
  height:70px;
}
#Works>.row,.projects .container{
  margin-top:5%;
  margin-bottom: 5%;
}

.language-icons{
  font-size: 2rem;
  font-size: var(--first-font-size);
  color:#8083ff;
  color:var(--third-color);
}
.language-name{
  font-size: 1rem;
  font-size: var(--second-font-size);
  font-weight: bold;
  text-transform: uppercase;
  font-family: 'Comfortaa', cursive;
  font-family: var(--first-font-family);
}

.working-card>img{
  width: 14rem;
  height: auto;

}
.working-card>h3,p,h3 a ,small p {
  font-family: 'Comfortaa', cursive;
  font-family: var(--first-font-family);
  font-size: 1rem;
  font-size: var(--second-font-size);
}

#projects .container .card .card-title,.card-body,.card-text {
  font-family: 'Comfortaa', cursive;
  font-family: var(--first-font-family);
  font-size: 1rem;
  font-size: var(--second-font-size);
  color: #3c4a3e;
  color: var(--Black-color);
}

.embed{
  border: 0;
  width: 100%;
  overflow: hidden;
  height: 100vh;
}

.title{
  font-size: 2rem;
  font-size: var(--first-font-size);
  font-family: 'Comfortaa', cursive;
  font-family: var(--first-font-family);
  text-align: center;
}

#Cv{
  height: 100vh;
  width: 100vw;
}

.shapedividers_com-3063{
  overflow:hidden;
  position:relative;
  }
  .shapedividers_com-3063::before{
  content:'';
  font-family:'shape divider from ShapeDividers.com';
  position: absolute;
  bottom: -1px;
  left: -1px;
  right: -1px;
  top: -1px;
  z-index: 3;
  pointer-events: none;
  background-repeat: no-repeat; 
  transform:scaleX(4);
  transform-origin: 100% 100%;
  animation: 10s infinite alternate shape-anim-3063-mobile linear;
  background-size: 100% 70px;
  background-position: 50% 100%;    
  background-image: url('data:image/svg+xml;charset=utf8, <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320" preserveAspectRatio="none"><path fill="%238083ff" d="M0 32l48 32c48 32 144 96 240 106.7 96 10.3 192-31.7 288-21.4 96 10.7 192 74.7 288 69.4 96-5.7 192-79.7 288-106.7s192-5 240 5.3l48 10.7v192H0z"/><path fill="%238083ff" opacity=".66" d="M0 64l48 16c48 16 144 48 240 48s192-32 288-26.7C672 107 768 149 864 144s192-59 288-80 192-11 240-5.3l48 5.3v256H0z"/></svg>'); 
  }
  
  @keyframes shape-anim-3063-mobile {
    100% {
      transform: scaleX(4) translateX(calc(100% - (100% / 4)));
